*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  box-sizing: border-box;
  background-color: #ced4da;
  font-family: 'Montserrat', sans-serif;
  color: #e9ecef; }
  body a {
    text-decoration: none;
    color: #333; }
    body a:hover {
      color: #74BDCB; }
  body li {
    list-style: none; }
  body ul {
    margin-bottom: 0; }
  body .img-fluid {
    max-width: 100%;
    height: auto; }
  body h2.title {
    color: #333;
    text-align: center;
    margin-bottom: 2rem;
    font-size: 4rem;
    text-transform: uppercase;
    font-family: "oswald";
    font-weight: 700; }
  body .btn {
    text-transform: uppercase;
    border: 3px solid #333;
    margin-top: 2rem;
    font-weight: 700;
    border-radius: 0; }
    body .btn:hover {
      background-color: #333;
      color: #fff;
      transition: .3s; }
    body .btn.btn-white {
      border: 3px solid #fff;
      color: #fff; }
      body .btn.btn-white:hover {
        background-color: #fff;
        color: #333;
        transition: .3s; }

header {
  background-color: #2c3e50;
  height: 60px;
  width: 100%;
  padding: 0 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  /*******************
NAV TOGGLE
*******************/ }
  header > .container {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  header a {
    color: #fff;
    text-decoration: none; }
    header a:hover {
      color: #74BDCB; }
    header a .top-logo {
      width: 170px;
      height: auto; }
  header nav {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    header nav .menu {
      display: flex; }
      header nav .menu .menu-item {
        margin-left: 1rem;
        font-size: .875rem;
        text-transform: uppercase; }
  header .toggle-nav {
    display: none;
    background-color: #333;
    width: 40px;
    height: 40px;
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 30px; }
    header .toggle-nav .line,
    header .toggle-nav .line::before,
    header .toggle-nav .line::after {
      content: '';
      background: #fff;
      height: 3px;
      width: 25px;
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }
    header .toggle-nav .line::before {
      width: 25px;
      top: calc(50% - 10px);
      transition: .3s ease; }
    header .toggle-nav .line::after {
      width: 25px;
      top: calc(50% + 10px);
      transition: .3s ease; }
    header .toggle-nav.close-nav .line {
      background: transparent;
      transition: .3s; }
    header .toggle-nav.close-nav .line::before {
      top: 50%;
      transform: translateX(-50%) translateY(-50%) rotateZ(-45deg); }
    header .toggle-nav.close-nav .line::after {
      top: 50%;
      transform: translateX(-50%) translateY(-50%) rotateZ(45deg); }

@media (max-width: 767px) {
  header > .container .toggle-nav {
    display: block; }
  header nav .menu {
    flex-direction: column;
    align-items: center;
    background-color: #333;
    width: 200px;
    height: 140px;
    position: absolute;
    top: -140px;
    right: 0;
    opacity: 0;
    transition: .5s; }
    header nav .menu .menu-item {
      margin-left: 0;
      margin-top: 10px;
      height: 40px; }
    header nav .menu.active {
      opacity: 1;
      top: 60px; } }

