    height: 400px; }
  .hero-image.services {
    background: no-repeat center url("../images/12.jpg");
    background-size: cover;
    height: 400px; }
  .hero-image::after {
    content: "";
    background-color: #000;
    opacity: .4;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1; }
  .hero-image .hero-content {
